html {
  scroll-behavior: smooth;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

body, p, h1, h2, h3, h4, h5, h6 {
  margin: 0;
  padding: 0;
}

ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

a {
  font-style: normal;
  text-decoration: none;
}

button {
  cursor: pointer;
}

.hidden {
  width: 1px;
  height: 1px;
  clip: rect(0, 0, 0, 0);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.is-hidden {
  opacity: 0;
  pointer-events: none;
}

body {
  background-color: #696969;
}

.header {
  width: 100%;
  height: 80px;
  z-index: 1;
  background-image: url("binary.738129f9.jpg");
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  border-bottom: 1px solid #000;
  padding-top: 6px;
  padding-left: 20px;
  font-family: Raleway sans-serif;
}

.header__title {
  color: #247c33;
}

.sidebar {
  height: 100vh;
  width: 130px;
  z-index: 1;
  background-color: #272626;
  border-right: 1px solid #000;
  position: static;
  top: 80px;
}

@media screen and (min-width: 768px) {
  .sidebar {
    width: 220px;
    height: 100vh;
  }
}

@media screen and (min-width: 1200px) {
  .sidebar {
    width: 330px;
    height: 100vh;
  }
}

.sidebar__img {
  width: auto;
  height: auto;
}

.sidebar__title {
  color: #fff;
  font-family: Raleway sans-serif;
  font-size: x-small;
  font-weight: 500;
  text-decoration: underline;
}

@media screen and (min-width: 768px) {
  .sidebar__title {
    font-size: medium;
  }
}

@media screen and (min-width: 1200px) {
  .sidebar__title {
    font-size: large;
  }
}

.sidebar__box {
  margin-top: 20px;
  margin-left: 10px;
}

.contacts__item {
  margin-top: 10px;
}

.contacts__link {
  color: #fff;
  font-family: Roboto sans-serif;
  font-size: x-small;
  font-weight: 500;
}

@media screen and (min-width: 768px) {
  .contacts__link {
    font-size: medium;
  }
}

@media screen and (min-width: 1200px) {
  .contacts__link {
    font-size: large;
  }
}

.contacts__icon {
  fill: #7fffd4;
  width: 10px;
  height: 8px;
}

@media screen and (min-width: 768px) {
  .contacts__icon {
    width: 16px;
    height: 12px;
  }
}

.accordion {
  cursor: pointer;
  width: 100%;
  text-align: left;
  background-color: #272727;
  border: none;
  outline: none;
  justify-content: space-between;
  align-items: baseline;
  padding: 18px;
  transition: all .4s;
  display: flex;
}

.accordion.active, .accordion:hover, .accordion :focus {
  background-color: #858d85;
}

.accordion:after {
  content: "➕";
  color: #7fffd4;
  float: right;
  margin-left: 5px;
  font-size: 13px;
}

.panel {
  max-height: 0;
  background-color: #194d4d;
  padding: 0 18px;
  transition: max-height .2s ease-out;
  overflow: hidden;
}

.active:after {
  content: "➖";
}

.skills__item {
  color: #f5f5f5;
  margin-bottom: 10px;
  margin-left: 10px;
  font-family: Roboto sans-serif;
  font-size: x-small;
}

.skills__item:first-child {
  margin-top: 10px;
}

.skills__item:before {
  content: "✚";
  color: #7fffd4;
  width: 5px;
  height: auto;
  margin-right: 15px;
  display: inline-block;
}

@media screen and (min-width: 768px) {
  .skills__item {
    font-size: medium;
  }
}

@media screen and (min-width: 1200px) {
  .skills__item {
    font-size: large;
  }
}

.skills__text {
  color: #fff;
  margin-left: 10px;
  font-family: Roboto sans-serif;
  font-size: x-small;
  font-weight: 600;
}

@media screen and (min-width: 768px) {
  .skills__text {
    font-size: medium;
  }
}

@media screen and (min-width: 1200px) {
  .skills__text {
    font-size: large;
  }
}

